@import url(https://fonts.googleapis.com/css2?family=Fascinate&family=Lato&family=Poppins&family=Titan+One&display=swap);
.app__app {
    background-color: rgb(7, 3, 32);
    width: 100vw;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
.navbar {    
    width: 100vw;
    display: flex;    
    align-items: flex-end;
    padding: 1rem;
    background-color: rgb(7, 45, 4);
}

.navbar-links {
    display: flex;
    align-items: flex-end;    
}

.navbar-links * {
    text-decoration: none;
    color: white;
    margin-right: auto;
}

.navbar-links h1{
    font-size: 2rem;
    font-family: cursive;
    letter-spacing: 5px;
}

.navbar-links_logo {
   display: flex;  
   flex-direction: column;
   align-items: center;  
}

.navbar-links_logo img {
    height: 100px;
    width: 100px;
    border-radius: 50%;
}

.navbar-links_container {
    display: flex;
    padding: 10px;
    margin-left: 40px;
}

.navbar-links_container a {
    margin: 0 16px;
}

.navbar-links_container input {
    border-radius: 5px;
    padding: 2px 15px;
    outline: none;
    border: none;
    color: black;
}

.navbar-sign {
    margin-left: auto;
}

.navbar button {
    background-color: blue;
    margin: 0 10px;
    border-radius: 5px;
    color: white;
    cursor: pointer;
    outline: none;
    border: solid 1px gray;
}

.nfts * {
  /* border: solid 1px red;   */  
}

.nfts {
  padding: 25px;
}

.nfts-container_cards {
  display: flex;
  flex-wrap: wrap;
  margin: 16px;
  min-height: 80vh;
}

.nfts-container_text {
  padding-left: 50px;  
}

.nfts-container_text h1 {
  font-family: 'Titan One', cursive;
  font-size: 40px;
  color: white;
}

.nft-card {
  height: 450px;
  width: 350px;
  margin: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;  
  border: 1px solid rgba(145, 27, 27, 0.538);
  border-radius: 4px;
}

.nft-card a {
  height: 100%;
  width: 100%;
  text-decoration: none;
}

.nft-card_top {
  height: 80%;
  width: 100%;
}

.nft-card_top img {
  height: 100%;
  width: 100%;
  border-radius: 4px;
}

.nft-card_bottom {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 20%;
}

.nft-card_bottom .card-title {
  font-size: 25px;
  color: white;
  font-family: 'Fascinate', cursive;
}

.nft-card_bottom .card-price {
  color: green;
  font-weight: 700;
}
.footer {
    width: 100vw;
    height: 100px;
    background-color: rgba(1, 232, 1, 0.659);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;    
}

.footer p {
    font-family: cursive;
}

.footer img {    
    width: 30px;
    height: 30px;
    border-radius: 50%;
}

.nft_details * {
    /* border: solid 1px red; */
}

.nft_details {
    display: flex;
    justify-content: center;
    width: 100vw;
    min-height: 100vh;
}

.nft_details-img, .nft_details-details {
    width: 50%;
}

.nft_details-img img {
    margin: 30px;    
    margin-left: 90px;
    width: 600px;
    height: 650px;
    border-radius: 10px;
}

.nft_details-details {
    padding-top: 50px;
}

.nft_details-details .details-row {
    padding: 15px 0;
}

.details-label {
    font-size: 30px;     
    font-weight: 700;
    color: white;
    font-family: 'Poppins', sans-serif;
}

.details-content {
    font-size: 1.3rem;
    font-style: italic;
    font-family: 'Poppins', sans-serif;
    color: rgb(154, 154, 154);
}

.details-bottom {
    display: flex;
    align-items: center;
    margin-top: 60px;
}

.details-buy_btn {
    border: 1px solid rgba(255, 0, 0, 0.452);
    width: 120px;
    height: 40px;
    border-radius: 5px;
    background-color: purple;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    /* margin-top: 60px; */
    cursor: pointer;
}

.details-gift_nft {
    margin-left: 60px;
    display: flex;
}

.details-gift_nft input {
    padding: 7px 10px;
    color: black;
    border-radius: 5px;
    border: 1px solid green;
    outline: none;
}

.details-gift_nft span {
    border: 1px solid rgba(255, 0, 0, 0.452);
    width: 120px;
    height: 40px;
    border-radius: 5px;
    background-color: rgb(0, 128, 15);
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    margin-left: 5px;
    cursor: pointer;
}
.create {
    min-height: 80vh;
    color: white;
}

.form-new {
    display: flex;
    justify-content: center;
    align-items: center;
}

.form-new .form-form {
    border: 1px solid gray;
    width: 500px;
    padding: 25px;
    margin: 32px;
    border-radius: 10px;
}

.create_form {
    font-size: 30px;
    margin-top: 20px;
    margin-left: 30px;
    font-family: 'Fascinate', cursive;
}

.create-canvas {
    width: 100%;
    height: 400px;    
    border-radius: 7px;
    margin-bottom: 10px;
    justify-content: center;
    align-items: center;
    display: flex;
}

* {
  font-family: 'Lato', sans-serif;
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on u, ol elements with a list role, which suggests default styling will be removed */
ul[role='list'],
ol[role='list'] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  -webkit-text-decoration-skip: ink;
          text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
   scroll-behavior: auto;
  }
  
  *,
  *::before,
  *::after {
    -webkit-animation-duration: 0.01ms !important;
            animation-duration: 0.01ms !important;
    -webkit-animation-iteration-count: 1 !important;
            animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

